  display: block;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  position: fixed;
  right: 4.000rem;
  bottom: 6.500rem;
  line-height: 3.000rem;
  border-radius: 0.125rem;
  box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.16), 0 0.125rem 0.625rem 0 rgba(0, 0, 0, 0.12);
  transition: visibility 0s, opacity 0.2s ease;
  z-index: 1; }

.holder-share-buttons.is-active {
  visibility: visible;
  opacity: 1; }

/*!
 * holder-vk-like
 */
.holder-vk-like {
  display: block;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  height: 3.000rem;
  position: fixed;
  right: 4.000rem;
  bottom: 3.500rem;
  line-height: 0;
  background-color: #5E81A8;
  border-radius: 0.125rem;
  box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.16), 0 0.125rem 0.625rem 0 rgba(0, 0, 0, 0.12);
  transition: visibility 0s, opacity 0.2s ease;
  z-index: 1; }

.holder-vk-like.is-active {
  visibility: visible;
  opacity: 1; }

.vk-like {
  display: inline-block;
  height: 24px;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  margin: 0 0 0 0.625rem;
  line-height: 0; }

/*!
 * @see {@link https://github.com/sksmatt/UItoTop-jQuery-Plugin/}
 */
.btn-totop {
  display: inline-block;
  visibility: hidden;
  opacity: 0;
  width: 3.000rem;
  height: 3.000rem;
  position: fixed;
  right: 0;
  bottom: 0;
  font-size: 1.500rem;
  line-height: 3.000rem;
  text-align: center;
  text-decoration: none;
  /* color: #FFFFFF;
	background-color: $color-rgba-page-overlay-bg; */
  border: none;
  transition: visibility 0s, opacity 0.2s ease, background-color 0.2s ease;
  -webkit-tap-highlight-color: transparent;
  outline: none; }

.btn-totop .ui-icon {
  /*!
 * Centering things
 * @see {@link https://www.w3.org/Style/Examples/007/center.en.html}
 */
  display: block;
  width: 1.500rem;
  height: 1.500rem;
  margin-top: 0.750rem;
  margin-right: auto;
  margin-left: auto; }

.btn-totop .ui-icon > * {
  fill: currentColor; }

.btn-totop.is-active {
  visibility: visible;
  opacity: 1; }

/* .btn-totop:focus,
.btn-totop:hover,
.btn-totop:active {
	background-color: $color-rgba-page-overlay-bg;
} */
.btn-totop.mui-btn--fab {
  width: 3.000rem;
  height: 3.000rem;
  position: fixed;
  right: 0.500rem;
  bottom: 0.500rem;
  line-height: 3.000rem;
  color: #FFFFFF;
  /* background-color: $clr-grey-600; */
  background-color: rgba(117, 117, 117, 0.5);
  box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.16), 0 0.125rem 0.625rem 0 rgba(0, 0, 0, 0.12);
  margin: 0; }

/*!
 * iframe-lightbox
 * hide default css preloader
 */
.iframe-lightbox .content > .body .half-circle-spinner {
  display: none; }

.iframe-lightbox iframe {
  box-shadow: 0.25rem 0.25rem 0.25rem 0 rgba(3, 3, 3, 0.3); }

/*!
 * img-lightbox
 * hide default css preloader
 * daneden.github.io/animate.css/
 */
.img-lightbox .half-circle-spinner {
  display: none; }

.img-lightbox img {
  box-shadow: 0.25rem 0.25rem 0.25rem 0 rgba(3, 3, 3, 0.3); }

#sidedrawer,
#header,
#footer,
#page {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity 0.2s ease; }

/*!
 * sections
 */
#header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  transition: top 0.4s ease-out;
  user-select: none;
  box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.16), 0 0.125rem 0.625rem 0 rgba(0, 0, 0, 0.12); }

#header.is-fixed {
  /* position: fixed; */
  top: 0;
  box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.16), 0 0.125rem 0.625rem 0 rgba(0, 0, 0, 0.12); }

#header.is-hidden {
  top: -3.500rem; }

@media (orientation: landscape) and (max-height: 480px) {
  #header.is-hidden {
    top: -3.000rem; } }

@media (min-width: 480px) {
  #header.is-hidden {
    top: -4.000rem; } }

#content-wrapper {
  min-height: 100%;
  overflow-x: hidden;
  /* margin-left: 0;
	transition: margin-left 0.2s; */
  /* sticky bottom */
  /* box-sizing: border-box;
	margin-bottom: -3.500rem;
	padding-bottom: 3.500rem; */ }

#content-wrapper .mui-container-fluid {
  padding-left: 0;
  padding-right: 0; }

@media (orientation: landscape) and (max-height: 480px) {
  #content-wrapper .mui-container-fluid {
    padding-left: 0;
    padding-right: 0; } }

@media (min-width: 480px) {
  #content-wrapper .mui-container-fluid {
    padding-left: 0.938rem;
    padding-right: 0.938rem; } }

#footer {
  color: #616161;
  text-align: center;
  /* background-color: $clr-light-blue-800;
	border-top: 0.063rem solid $clr-light-blue-800; */
  user-select: none;
  padding: 0 0 0.938rem 0;
  /*!
 * @see {@link https://css-tricks.com/couple-takes-sticky-footer/}
 * Sticky Footer
 * The position relative on body is required to make it work.
 */
  /* height: 3.500rem; */
  /* position: absolute;
	left: 0;
	right: 0;
	bottom: 0; */ }

#footer a {
  color: inherit;
  text-decoration: none; }

#footer a:focus,
#footer a:hover,
#footer a:active {
  text-decoration: underline; }

#footer .copyright {
  font-size: 0.867rem;
  line-height: 1.333rem; }

/*!
 * sidedrawer
 */
#sidedrawer {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 15.000rem;
  left: -15.000rem;
  overflow: auto;
  z-index: 2;
  color: #616161;
  background-color: #FFFFFF;
  transition: transform 0.2s;
  -webkit-transform: translateZ(0);
  box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.16), 0 0.125rem 0.625rem 0 rgba(0, 0, 0, 0.12);
  user-select: none; }

#sidedrawer-brand {
  /* 119x56 */
  height: 3.500rem;
  padding-left: 1.250rem; }

@media (orientation: landscape) and (max-height: 480px) {
  #sidedrawer-brand {
    /* 102x48 */
    height: 3.000rem; } }

@media (min-width: 480px) {
  #sidedrawer-brand {
    /* 136x64 */
    height: 4.000rem; } }

#sidedrawer-brand .site-logo {
  position: absolute; }

#sidedrawer ul {
  list-style: none; }

#sidedrawer > ul {
  padding-left: 0;
  margin: 0; }

#sidedrawer > ul > li:first-child {
  padding-top: 0; }

#sidedrawer li a,
#sidedrawer li > strong {
  display: block;
  height: 3.000rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.867rem;
  line-height: 3.000rem;
  text-decoration: none;
  border: none;
  padding: 0 1.250rem;
  cursor: pointer; }

#sidedrawer li a:focus,
#sidedrawer li a:hover,
#sidedrawer li a:active,
#sidedrawer li a.is-active,
#sidedrawer li > strong:focus,
#sidedrawer li > strong:hover,
#sidedrawer li > strong:active {
  text-decoration: none;
  background-color: #40D7CF; }

#sidedrawer li strong + ul > li a {
  padding-left: 0; }

#sidedrawer li strong + ul > li a {
  background-color: transparent; }

#sidedrawer li strong + ul > li a:focus,
#sidedrawer li strong + ul > li a:hover,
#sidedrawer li strong + ul > li a:active {
  text-decoration: none;
  background-color: transparent; }

#sidedrawer.is-active {
  transform: translate(15rem); }

#sidedrawer .mui-appbar__ui-button--menu {
  position: relative;
  float: left;
  color: inherit; }

#sidedrawer .copyright {
  font-size: 0.867rem;
  line-height: 1.333rem;
  padding: 0.938rem 1.250rem;
  margin: 0; }

#sidedrawer .copyright a {
  text-decoration: none; }

#sidedrawer .mui-divider {
  background-color: currentColor; }

@media (min-width: 768px) {
  /* #header {
		left: 15.000rem;
	} */
  #sidedrawer {
    transform: translate(15rem); }
  /* #content-wrapper {
		margin-left: 15.000rem;
	} */
  /* #footer {
		margin-left: 15.000rem;
	} */
  body.hide-sidedrawer #header {
    left: 0; }
  body.hide-sidedrawer #sidedrawer {
    transform: translate(0); }
  body.hide-sidedrawer #content-wrapper {
    margin-left: 0; }
  body.hide-sidedrawer #footer {
    margin-left: 0; } }

